*{
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
body{
  height: 100vh;
  margin: 0;
  width: 100vw;
  
  font-family: "Nunito", Arial, Helvetica, sans-serif;
  background-color: rgb(218, 218, 247);
}
.roll{
  width: 160%;
  margin-top: 100px;
  
}
.paracont{
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
  height: 50px;
  margin-left: 50%;
}

.custom{
  width: 100%;
  font-family: 'Times New Roman', Times, serif;
  font-weight: 200;
  text-transform: uppercase;
  word-spacing: 5px;
  font-size: 50px;
  margin-left: -300px;
  
}


section{
  height: 100vh;
  position: relative;
  
}
.fest{
  
  height: 100vh;
  display: flex;
  overflow: hidden;
}
.fest p{
  position: absolute;
  top: 40%;
  left: 10%;
  width: 30%;
 
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  font-size: 16px;
 
}

.imagecont{
  position: absolute;
  right: 0;
  width: 50%;
  margin: 0;
  padding: 0;
  border-radius: 5px;
  overflow: hidden;
 
}
.imagecont img{
  width: 100%;
  height: 100%;
  object-fit: cover;
 
}


#travel{
  text-transform: capitalize;
  font-family: 'Times New Roman', Times, serif;
  font-weight: 200;
  margin: 100px;
  

}

.App {
  text-align: center;
}
#second{
  width: 90%;
  height: 90%;
  
  margin-left: 50%;
  transform: translateX(-50%);
  background-image: url("./images/tree.jpg");
  background-size: cover;
  overflow: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 5px;
}
.cus{
color:rgb(218, 218, 247);
text-transform: capitalize;
font-family: 'Times New Roman', Times, serif;
font-size: 40px;
margin-left: -100px;
font-weight: 100;
word-spacing: 10px;
width: 80%;

}
.image2{
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: width 1s;
  
}
.scrolltwo{
  width: 150%;
  height: 150%;
}

.travelcont{
  
  height: 35px;
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
  margin: 100px;
  
}
.unor{
  
  width: 100%;
  height: 70%;
  list-style-type: none;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin-top: 100px;
}
.unor li{
  width: 35%;
  height: 100%;
  
 
}
.won{
  display: flex;
 align-items: center;
}
.tome{
  
  font-size: 20px;
  margin-left: 10%;
  font-weight: lighter;
  
  
}
.unor li:last-child{
  width: 60%;
  
  display: flex;
  justify-content: space-between;
  align-items: center;
 
}
.unor li:last-child img{
  width: 50%;
  height: 100%;
  object-fit: cover;
}

.ali{
  background: yellow;
  height: 50%;
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  list-style-type: none;
}
.ali li:first-child{
  width: 30%;
  background: red;
  height: 100%;
}


@media(max-width:450px){
  section{
    height: 50vh;
  }
  .unor li:last-child{
    width: 100%;
    background-color: blue;
    display: flex;
    justify-content: space-between;
    align-items: center;
   
  }
  .unor li:last-child img{
    width: 50%;
    height: 100%;
    object-fit: cover;
  }
  
  .won{
    display: flex;
   align-items: center;

   
  }
  .tome{
    font-size: 14px;
    margin-left: 5%;
   margin-top: -70%;
  }
  .unor li{
    height: 50%;
    width: 100%;
  }
  .unor{
    flex-direction: column;
    
    margin-top: 200px;
  }
  
  #second{
    margin-top: 150px;
  }
  .cus{
    font-size: 25px;
    margin-left: 50px;
    font-weight: 100;
    word-spacing: 10px;
    
    
    }
  
  body{
   overflow-x: hidden;
    
  }
  #travel{
    font-weight: 200;
    margin: 0;
   
    
  }
  .travelcont{
    height: 45px;
    margin:0px;
    margin-left: 0%;
    margin-top: 30px;
    width: 100%;
    padding: 30px;
    
    
    
  }
  .imagecont{
    position: absolute;
    right: 0;
    width: 40%;
    height: 80%;
    margin-top: 10%;
  }
  .fest{
    height: 100vh;
  }
  .fest p{
    top: 40%;
    left: 5%;
    width: 50%;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    font-size: 14px;
    font-weight: lighter;
  }
  .roll{
    width: 350%;
    margin-top: -40px;
    
    position: relative;
    
  }
  
  .custom{
    margin-left: -42%;
    font-family: 'Times New Roman', Times, serif;
    font-weight: 200;
    text-transform: uppercase;
    word-spacing: 5px;
    font-size: 30px;
   
  }
}




